.urgent-swiper-container {
  width: calc(100% - 24px);
  height: 100%;
  overflow: hidden;
  margin: 0 auto;
  position: relative;

  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background-color: $white;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .urgent-swiper-button-next,
  .urgent-swiper-button-prev {
    position: absolute;
    top: 50%;
    width: 34px;
    height: 34px;
    border-radius: 50%;
    cursor: pointer;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
    transform: translateY(-50%);
    background-color: $strongblue;
    background-repeat: no-repeat;
    background-position: center;
    z-index: 1000;
  }

  .urgent-swiper-button-prev {
    left: 2px;
    background-image: url("../img/urgent-arrow-left.svg");
  }

  .urgent-swiper-button-next {
    right: 2px;
    background-image: url("../img/urgent-arrow-right.svg");
  }
}

@media (max-width: $tablet-width) {
  .urgent-swiper-container {
    min-width: 360px;
  }
}
